"use client"

import { useState, useEffect } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { BookOpen, Play, Moon, Sparkles } from "lucide-react"

export function MonthCalendar({ month, days, year, userEmail, monthIndex }: any) {
  const [readings, setReadings] = useState<any[]>([])
  const [tempBookNames, setTempBookNames] = useState<Record<number, string>>({})
  const [isUpdating, setIsUpdating] = useState(false)

  async function fetchReadings() {
    if (!userEmail) return
    // O t=${Date.now()} evita que o navegador use dados antigos (cache)
    const res = await fetch(`/api/reading-data?email=${encodeURIComponent(userEmail)}&t=${Date.now()}`, {
      cache: 'no-store'
    })
    const json = await res.json()
    setReadings(json.data || [])
  }

  useEffect(() => {
    fetchReadings()
  }, [userEmail, monthIndex, year])

  async function handleStart(day: number) {
    const bookName = tempBookNames[day]
    if (!bookName || isUpdating) return
    
    setIsUpdating(true)
    const startDate = new Date(year, monthIndex, day, 12, 0, 0).toISOString()

    const res = await fetch("/api/reading-data", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        email: userEmail,
        bookName,
        startDate,
        year,
        month: monthIndex + 1,
        day,
        action: "START_READING",
      }),
    })

    if (res.ok) {
      setTempBookNames((prev) => ({ ...prev, [day]: "" }))
      await fetchReadings()
    }
    setIsUpdating(false)
  }

  async function handleFinish(bookName: string, day: number) {
    if (isUpdating) return
    setIsUpdating(true)

    const endDate = new Date(year, monthIndex, day, 12, 0, 0).toISOString()
    const res = await fetch("/api/reading-data", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ 
        email: userEmail, 
        bookName, 
        endDate, 
        action: "FINISH_READING" 
      }),
    })
    
    if (res.ok) {
      await fetchReadings()
    }
    setIsUpdating(false)
  }

  return (
    <Card className="border-accent/20 bg-card p-6 shadow-lg md:p-10">
      <div className="mb-8 text-center">
        <div className="mb-2 flex items-center justify-center gap-2">
          <Sparkles className="h-5 w-5 text-primary" />
          <h2 className="font-serif text-3xl font-bold text-foreground md:text-4xl">{month}</h2>
          <Sparkles className="h-5 w-5 text-primary" />
        </div>
        <p className="text-lg text-muted-foreground">{year}</p>
      </div>

      <div className="grid grid-cols-2 gap-3 md:grid-cols-7 md:gap-4">
        {Array.from({ length: days }, (_, i) => i + 1).map((day) => {
          const formattedMonth = String(monthIndex + 1).padStart(2, '0')
          const formattedDay = String(day).padStart(2, '0')
          const currentStringDate = `${year}-${formattedMonth}-${formattedDay}`

          const dayReadings = readings.filter((r) => {
            if (!r.start_date) return false
            const startStr = r.start_date.split('T')[0]
            const endStr = r.end_date ? r.end_date.split('T')[0] : null

            const isStarted = currentStringDate >= startStr
            const isNotFinished = !endStr || currentStringDate <= endStr

            return isStarted && isNotFinished
          })

          return (
            <div key={`${year}-${monthIndex}-${day}`} className="min-h-45 rounded-lg border border-border bg-background/50 p-3 flex flex-col transition-all hover:border-primary/40">
              <div className="flex justify-between items-start mb-2">
                <span className="font-bold text-primary">{day}</span>
                {day % 7 === 0 && <Moon size={14} className="text-muted-foreground" />}
              </div>

              <div className="flex-1 space-y-1.5 overflow-y-auto mb-3">
                {dayReadings.map((r, idx) => (
                  <div key={`${r.id}-${idx}`} className={`p-2 rounded-md border text-[10px] font-bold ${r.status === 'lendo' ? 'bg-primary/10 border-primary/30 text-foreground' : 'bg-muted/50 border-border text-muted-foreground opacity-60'}`}>
                    <div className="flex items-center gap-1 truncate mb-1">
                      <BookOpen size={10} className={r.status === 'lendo' ? "text-primary" : "text-muted-foreground"} />
                      {r.book_name.toUpperCase()}
                    </div>
                    {r.status === "lendo" && (
                      <Button 
                        variant="destructive" 
                        size="sm" 
                        className="h-5 w-full text-[8px] font-black" 
                        onClick={() => handleFinish(r.book_name, day)}
                        disabled={isUpdating}
                      >
                        ENCERRAR
                      </Button>
                    )}
                  </div>
                ))}
              </div>

              <div className="mt-auto pt-2 border-t border-border/50">
                <Input
                  placeholder="Livro..."
                  value={tempBookNames[day] || ""}
                  onChange={(e) => setTempBookNames((prev) => ({ ...prev, [day]: e.target.value }))}
                  className="h-7 text-[10px] mb-1 bg-background/80"
                />
                <Button 
                  size="sm" 
                  onClick={() => handleStart(day)} 
                  disabled={isUpdating}
                  className="h-7 w-full text-[10px] font-bold bg-secondary hover:bg-primary hover:text-white"
                >
                  <Play size={8} className="mr-1" /> INICIAR
                </Button>
              </div>
            </div>
          )
        })}
      </div>
    </Card>
  )
}